*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-transform: capitalize;
}

:root{
  --clr-grey-9: hsl(209, 61%, 16%);
  --clr-white: #fff;
  --clr-pink: #f28ab2;
  --clr-red-light: hsl(360, 71%, 66%);
}

body{
  background-color: var(--clr-pink);
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}

.white{
  background-color: #fff;
  width: 400px;
  height: auto;
  margin: auto;
  margin-top: 30px;
  border-radius: 10px;
  padding: 20px;
}

h3{
  margin-bottom: 20px;
  color: var(--clr-grey-9);
}

.btn{
  padding: 5px 10px;
  width: calc(100% - 30px);
  color: #fff;
  background: #f28ab2;
  border: none;
  border-radius: 5px;
  font-size: 20px;
}

/* List */
main{
  margin-top: 30px;
}
.list{
  display: flex;
  width: auto;
  gap: 10px;
  margin-bottom: 20px;
}
.list img{
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 50%;
}
h4{
  color: var(--clr-grey-9);
}
p{
  color: gray;
}

@media (max-width: 769px){
  .white{
    width: 300px;
  }
}